@-webkit-keyframes myfirst {
from {
    margin-top: 0;
}
to {
    margin-top: -10%;
}
}
@keyframes myfirst {
from {
    margin-top: 0;
}
to {
    margin-top: -10%;
}
}
@-webkit-keyframes subbottom {
from {
    bottom: -0.75rem;
