.rank-container {
  background: #fff;
}
.gradual {
  position: absolute;
  left: 0;
  right: 0;
  pointer-events: none;
  height: 229px;
}
.rank-hd {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  height: 7.6rem;
  background-size: 100%;
  overflow: hidden;
  transition: all 1s;
}
.rank-hd .rank-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 0.15;
  margin-top: 0;
}
.rank-hd .animation {
  -webkit-animation: myfirst 2s;
          animation: myfirst 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.swiper-pagination {
  position: initial !important;
  text-align: left !important;
}
.swiper-pagination .swiper-pagination-bullet-active {
  background: transparent;
}
.swiper-pagination .swiper-pagination-bullet {
  border-radius: 0;
  background: transparent;
  opacity: 1;
}
.rank-nav {
  position: absolute;
  top: 4.75rem;
  left: 0;
  width: 100%;
  overflow: hidden;
  padding: 0 0.5rem;
}
.rank-nav .rank-nav-main {
  width: 200%;
}
.rank-nav .nav-item {
  margin: 0 10px;
  width: initial;
  height: 2.5rem;
  line-height: 1;
  display: inline-table;
  position: relative;
}
.rank-nav .nav-item span {
  display: block;
  color: #fff;
  opacity: 0.6;
}
.rank-nav .nav-item .item-title {
  font-size: 0.9rem;
  line-height: 1.1;
}
.rank-nav .nav-item .sub-item-title {
  position: absolute;
  display: none;
  font-size: 0.6rem;
  bottom: -0.75rem;
}
.rank-nav .nav-item .sub-item-title.animationed {
  -webkit-animation: subbottom 0.3s;
          animation: subbottom 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.rank-nav .nav-item.swiper-pagination-bullet-active .item-title {
  font-weight: 700;
  opacity: 1;
}
.rank-nav .nav-item.swiper-pagination-bullet-active .sub-item-title {
  display: block;
}
.rank-nav.float-rank-nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999999;
  padding: 0;
  margin: 0;
  width: 100%;
  border-bottom: 1px solid #f0f0f0;
}
.rank-nav.float-rank-nav .rank-nav-main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  width: 100%;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background: #fff;
  padding: 0 0.5rem;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
.rank-nav.float-rank-nav .rank-nav-main .nav-item {
  height: 2.2rem;
  line-height: 2.2rem;
  margin: 0;
}
.rank-nav.float-rank-nav .rank-nav-main .nav-item span {
  color: #333;
}
.rank-nav.float-rank-nav .rank-nav-main .nav-item .item-title {
  font-size: 0.65rem;
  padding: 0;
}
.rank-nav.float-rank-nav .rank-nav-main .nav-item .sub-item-title {
  display: none;
}
.rank-nav.float-rank-nav .rank-nav-main .swiper-pagination-bullet-active .item-title {
  color: #ff7551;
  position: relative;
}
.rank-nav.float-rank-nav .rank-nav-main .swiper-pagination-bullet-active .item-title:before {
  display: block;
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: #ff7551;
}
.rank-nav .swiper-pagination-clickable .swiper-pagination-bullet {
  cursor: none;
}
@media only screen and (max-width: 320px) {
.rank-nav.float-rank-nav .rank-nav-main .nav-item .item-title {
    font-size: 0.6rem;
}
}



