@font-face {
  font-family: 'iconfont';
  /* project id 307140 */
  src: url('//at.alicdn.com/t/font_1pxfislyf46g8pvi.eot');
  src: url('//at.alicdn.com/t/font_1pxfislyf46g8pvi.eot?#iefix') format('embedded-opentype'), url('//at.alicdn.com/t/font_1pxfislyf46g8pvi.woff') format('woff'), url('//at.alicdn.com/t/font_1pxfislyf46g8pvi.ttf') format('truetype'), url('//at.alicdn.com/t/font_1pxfislyf46g8pvi.svg#iconfont') format('svg');
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
  vertical-align: baseline;
}
.null-data {
  text-align: center;
  padding: 1.5rem 0.75rem;
  color: #b8c0cc;
}
.null-data:before {
  display: block;
  content: '\e623';
  font-family: 'iconfont';
  font-size: 5rem;
  color: #b8c0cc;
  line-height: 1;
}
.null-data.hide {
  display: none;
}
.null-data span {
  margin-top: 0.5rem;
  display: block;
}
.header-bar ~ .content {
  padding-top: 2.2rem;
}
.mint-indicator-wrapper {
  z-index: 999;
}
.fade-enter-active,
.fade-leave-active {
  transition: all .4s ease;
}
.fade-enter {
  opacity: 0;
}
.fade-leave-active {
  opacity: 0;
}
[v-cloak] {
  display: none;
}
a,
a:active {
  color: #333;
}



